.me {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 40px 0 70px;
}
.me .my-avatar {
  background-color: #0053FA;
  color: white;
  height: 40px;
  margin-right: 10px;
}
.me .my-avatar .ant-avatar-string {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: 2px;
}
.me .my-name {
  flex: 1;
  font-weight: 600;
  font-size: 20px;
  color: #0d1b2a;
  letter-spacing: 0;
}
